:root {
  --main-font: "Roboto", sans-serif;
  --main-text-color: #2c2c2c;
  --btn-default: #bbf330;
  --btn-onhover: #b5ff00;
  --bgc-main: #fefefe;
  --bgc-secondary: #fff;
  --dark-green: #7eb101;
  --border-onhover: # #597d00;
  --yellow-main: #ffe550;
  --error-color: #ff705d;
  --radial-gradient: #2f583f@ 0% rgba(105, 105, 105, 0)@ 100%;
  --items: 3;
  --indent: 32px;
  --btn-input-radius: 30px;
  --animation: 250ms cubic-bezier(.4, 0, .2, 1);
}

h1, h2, h3, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center;
  display: block;
}

body {
  color: var(--main-text-color);
  background-color: var(--bgc-main);
  font-family: var(--main-font);
  font-size: 16px;
  line-height: 1.5;
}

